<template>
  <div class="hello" @keyup.13="myMethod">
    <h1>2048</h1>
    <h2>分数：{{integral}}</h2>
    <h2 @click="load">开始</h2>
    <div class="div_warp">
      <div v-for="k in 4">
        <div v-for="v in 4"
             :style="{left:((v-1)*120)+'px',top:((k-1)*120)+'px'}"
             style="position: absolute;margin: 20px;width: 100px;height: 100px;background-color: rgba(238, 228, 218, 0.35);border-radius: 4px;color: #fff">

        </div>
      </div>
      <div v-for="item in list">
        <div v-for="v in item"
             :class="{'div_fon55':v.value<1000,'div_fon34':v.value>1000}"
             v-show="v.value!=0"
             :style="{left:v.left,top:v.top,color:color(v.value)}"
             style="position: absolute;margin: 20px;width: 100px;height: 100px;background-color: rgba(238, 228, 218, 0.35);
           border-radius: 4px; ;line-height: 100px">
          {{v.value}}
        </div>
      </div>
    </div>

  </div>
</template>

<script>
  export default {
    name: 'hello',
    data () {
      return {
        list: [],
        integral: 0
      }
    },
    methods: {
      random(){
        if (this.is())
          return false;
        let randNumber = Math.random() < 0.5 ? 2 : 4;
        let i = parseInt(Math.random() * 4);
        let j = parseInt(Math.random() * 4);
        let _this = this;
        while (true) {
          if (_this.list[i][j].value == 0)
            break;
          i = parseInt(Math.random() * 4);
          j = parseInt(Math.random() * 4);
        }
        this.list[i][j].value = randNumber;
        this.integral=0;
        this.list.forEach((v) => {
          v.forEach((t) => {
            this.integral = this.integral + t.value;
          })
        })
      },
      is(){
        for (var i = 0; i < 4; i++) {
          for (var j = 0; j < 4; j++) {
            if (this.list[i][j].value == 0) {
              return false;
            }
          }
        }
        return true;
      },//判断是否还有空位
      myMethod(){
        console.log("xxx");
      },
      top(){
        for (var t = 0; t < 4; t++) {
          for (var i = 3; i > -1; i--) {
            for (var k = 3; k > -1; k--) {
              if (this.list[i - 1]) {
                if (this.list[i - 1][k].value == 0) {
                  this.list[i - 1][k].value = this.list[i][k].value;
                  this.list[i][k].value = 0;
                  this.list[i][k].show = this.list[i][k].value == 0 ? false : true;
                } else if (this.list[i - 1][k].value == this.list[i][k].value) {
                  this.list[i][k].value = this.list[i][k].value + this.list[i - 1][k].value;
                  this.list[i - 1][k].value = 0;
                }
              }
            }
          }
        }
        this.random();
      },
      down(){
        for (var t = 0; t < 4; t++) {
          for (var i = 3; i > -1; i--) {
            for (var k = 0; k < 4; k++) {
              if (this.list[k + 1]) {
                if (this.list[k + 1][i].value == 0) {
                  this.list[k + 1][i].value = this.list[k][i].value;
                  this.list[k][i].value = 0;
                  this.list[k][i].show = this.list[k][i].value == 0 ? false : true;
                } else if (this.list[k + 1][i].value == this.list[k][i].value) {
                  this.list[k][i].value = this.list[k][i].value + this.list[k + 1][i].value;
                  this.list[k + 1][i].value = 0;
                }
              }
            }
          }
        }

        this.random();
      },
      right(){
        for (var t = 0; t < 4; t++) {
          for (var i = 0; i < 4; i++) {
            for (var k = 3; k > -1; k--) {
              if (this.list[i][k - 1]) {
                if (this.list[i][k].value == 0) {
                  this.list[i][k].value = this.list[i][k - 1].value;
                  this.list[i][k - 1].value = 0;
                  this.list[i][k].show = this.list[i][k].value == 0 ? false : true;
                } else if (this.list[i][k].value == this.list[i][k - 1].value) {
                  this.list[i][k].value = this.list[i][k].value + this.list[i][k - 1].value;
                  this.list[i][k - 1].value = 0;
                }
              }
            }
          }
        }
        this.random();
      },
      left(){
        for (var t = 0; t < 4; t++) {
          for (var i = 0; i < 4; i++) {
            for (var k = 0; k < 4; k++) {
              if (this.list[i][k + 1]) {
                if (this.list[i][k].value == 0) {
                  this.list[i][k].value = this.list[i][k + 1].value;
                  this.list[i][k + 1].value = 0;
                  this.list[i][k].show = this.list[i][k].value == 0 ? false : true;
                } else if (this.list[i][k].value == this.list[i][k + 1].value) {
                  this.list[i][k].value = this.list[i][k].value + this.list[i][k + 1].value;
                  this.list[i][k + 1].value = 0;
                }
              }
            }
          }
        }
        this.random();
      },
      load(){
        this.list = [];
        for (var i = 0; i < 4; i++) {
          this.list.push([]);
          for (var k = 0; k < 4; k++) {
            this.list[i].push({
              top: (i * 120) + 'px',
              left: (k * 120) + 'px',
              value: 0
            });
          }
        }
        this.random();
        this.random();
      },
      color(number){
        switch (number) {
          case 2:
            return "#fff";
            break;
          case 4:
            return "#cccc";
            break;
          case 8:
            return "#f26179";
            break;
          case 16:
            return "#f59563";
            break;
          case 32:
            return "#f67c5f";
            break;
          case 64:
            return "#f65e36";
            break;
          case 128:
            return "#edcf72";
            break;
          case 256:
            return "#edcc61";
            break;
          case 512:
            return "#9c0";
            break;
          case 1024:
            return "#3365a5";
            break;
          case 2048:
            return "#A54472";
            break;
          case 4096:
            return "#eee";
            break;
          case 8192:
            return "red";
            break;
        }
        return "black";
      }
    },
    mounted(){
      let _this = this;
      window.addEventListener('keyup', function (e) {
        switch (e.keyCode) {
          case 37://left
            _this.left();
            break;
          case 38://top
            _this.top();
            break;
          case 39://right
            _this.right();
            break;
          case 40://down
            _this.down();
            break;
          default:
            break;
        }
      })
    },
    created()
    {
      this.load();
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">

  @import 'hello.less';
</style>



